import { useSearchParams } from 'react-router-dom';
import { Alert } from 'react-bootstrap';
import useSWR from 'swr';

import LoadingSpinner from "../../components/common/LoadingSpinner";
import Posts from "../../components/post/Posts";
import Paginations from '../../components/post/Paginations';
import { readAll } from '../../utils/postApi';
import { convertToInt } from '../../utils/constants';

function PostList() {
  // 1. pageno   posts fetch
  const [params] = useSearchParams();
  let pageno = convertToInt(params.get('pageno'), 1);
  const {data, error, isLoading } = useSWR(['posts', pageno], ()=>readAll(pageno), { revalidateOnFocus: false} );

  // 2. SWR     (conditional rendering)
  if(isLoading) return <LoadingSpinner />
  if(error) return <Alert variant='danger'>  </Alert>
  
  // 3.   
  const { posts, ...pagination } = data;

  return (
    <div>
      <Posts posts={posts} />
      <Paginations pagination={pagination} />
    </div>
  )
}

export default PostList