import { useEffect, useState } from "react";
import { Navigate, useNavigate } from 'react-router-dom';
import { Alert } from "react-bootstrap";
import useSWR, { mutate } from "swr";

import ProfileField from "../../components/member/ProfileField";
import usePhoto from "../../hooks/usePhoto";
import LoadingSpinner from "../../components/common/LoadingSpinner";
import usePasswordStore from '../../stores/usePasswordStore';
import { AsyncStatus } from "../../utils/constants";
import { changeProfile, read } from '../../utils/memberApi';

function MemberRead() {
  // 1.  
  //    
  const isPasswordVerified = usePasswordStore(state=>state.isPasswordVerified);
  //    :      
  const [status, setStatus] = useState(AsyncStatus.IDLE);
  const vProfile = usePhoto();
  const navigate = useNavigate();

  // 2.   
  //        ->   57 /member/check-password ,   useSWR API   
  //    useSWR   null      
  const { data, error, isLoading } = useSWR(isPasswordVerified ? ['me'] : null, () => read());

  // 3.  fetch     
  useEffect(()=>{
    if(data) 
      vProfile.setPhotoUrl(data.profile);
  }, [data]);

  // 4.   :    
  const isSubmitting = status === AsyncStatus.SUBMITTING;

  // 5.  
  const handleChangeProfile=async()=>{
    if(status===AsyncStatus.SUBMITTING) return;
    setStatus(AsyncStatus.SUBMITTING);

    if(!vProfile.value)
      return;
    try {
      const formData = new FormData();
      formData.append('profile', vProfile.value);
      const {data} = await changeProfile(formData);
      //  swr  
      mutate('me', data, false);
      setStatus(AsyncStatus.SUCCESS);
    } catch(err) {
      setStatus(AsyncStatus.FAIL);
      console.log(err);
    } 
  }
  
  // 5.  
  if(isPasswordVerified===false) return <Navigate to="/member/check-password" replace />
  if(isLoading)  return <LoadingSpinner />;
  if(error) return <Alert variant='danger'>  </Alert>;

  return (
    <div>
      <table className='table table-border'>
        <tbody>
          <tr>
            <td colSpan={2}>
              <ProfileField name='photo' label='' alt='' {...vProfile} />
              <button className='btn btn-primary' onClick={handleChangeProfile} disabled={vProfile.value===null}>{isSubmitting? "  ...":" "}</button>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>{data.username}</td>
          </tr>
          <tr>
            <td></td>
            <td>{data.email}</td>
          </tr>
          <tr>
            <td></td>
            <td>{data.level}</td>
          </tr>
          <tr>
            <td></td>
            <td>{`${data.joinday} (${data.days})`}</td>
          </tr>
          <tr>
            <td colSpan={2}>
              <button className='btn btn-success' onClick={()=>navigate('/member/change-password')}> </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  )
}

export default MemberRead