import { useState } from 'react';
import { Navigate, useNavigate } from 'react-router-dom';
import { Alert } from 'react-bootstrap';

import usePassword from '../../hooks/usePassword';
import {checkPassword} from '../../utils/memberApi';
import { AsyncStatus } from '../../utils/constants';
import usePasswordStore from '../../stores/usePasswordStore';
import TextField from '../../components/common/TextField';
import BlockButton from '../../components/common/BlockButton';


function MemberCheckPassword() {
  // 1.   ( ,   ,    setter,     , )
  const [status, setStatus] = useState(AsyncStatus.IDLE);
  const {isPasswordVerified, setPasswordVerified} = usePasswordStore();
  const vPassword = usePassword();
  const navigate = useNavigate();

  // 2.   :    
  const isSubmitting = status === AsyncStatus.SUBMITTING;

  // 3.   
  const handleCheckPassword=async ()=>{
    if(isSubmitting) return;
    setStatus(AsyncStatus.SUBMITTING);

    if (!(vPassword.onBlur())) {
      setStatus(AsyncStatus.IDLE);
      return;
    }
    
    try {
      await checkPassword(vPassword.value);
      setPasswordVerified();
      setStatus(AsyncStatus.SUCCESS);
      navigate("/member/read");
      return;
    } catch(err) {
      setStatus(AsyncStatus.FAIL);
      console.log(err);
    } 
  }

  if(isPasswordVerified===true) return <Navigate to="/member/read" replace />

  return (
    <div>
      {status===AsyncStatus.FAIL &&  <Alert variant='danger'>  </Alert>}
      <TextField type='password' label='' name='password' {...vPassword} />
      <BlockButton label={isSubmitting? "  ...":" "} onClick={handleCheckPassword} styleName='dark' disabled={isSubmitting}/>
    </div>
  )
}

export default MemberCheckPassword