import { Link } from "react-router-dom";
import useAuthStore from "../stores/useAuthStore";
import { logout } from "../utils/authApi";

function Nav() {
  const {role, setLogout} = useAuthStore();

  const doLogout=async (e)=>{
    e.preventDefault();
    try {
      await logout();
      setLogout();
    } catch(err) {
      console.log(err);
    }
  }

  console.log(role);
  if(!role) {
    return (
      <nav>
        <ul>
          <li><Link to={"/"} style={{color:'white'}}>HOME</Link></li>
          <li><Link to={"/member/signup"}></Link></li>
          <li><Link to={"/member/find-username"}> </Link></li>
          <li><Link to={"/member/find-password"}> </Link></li>
          <li><Link to={"/member/login"}></Link></li>
        </ul>
      </nav>
    )
  } else {
    if(role==='ROLE_USER') {
      return (
        <nav>
          <ul>
            <li><Link to={"/"} style={{color:'white'}}>HOME</Link></li>
            <li><Link to={"/member/read"}></Link></li>
            <li><Link to={"/post/write"} ></Link></li>
            <li><Link to={"#"} onClick={doLogout}></Link></li>
          </ul>     
        </nav>
      )
    } else if(role==='ROLE_ADMIN') {
      return (
        <nav>
          <ul>
            <li><Link to="/" style={{color:'white'}}>HOME</Link></li>
            <li><Link to="/admin" >ADMIN</Link></li>
            <li><Link to={"#"} onClick={doLogout}></Link></li>
          </ul>
        </nav>
      )
    }
  }
}

export default Nav