import { useState } from "react";
import { Alert } from "react-bootstrap";

import TextField from "../../components/common/TextField";
import BlockButton from "../../components/common/BlockButton";
import { AsyncStatus } from "../../utils/constants";
import useUsername from "../../hooks/useUsername";
import { resetPassword } from "../../utils/memberApi";

function MemberResetPassword() {
  const [status, setStatus] = useState(AsyncStatus.IDLE);
  const vUsername = useUsername();

  const handleResetUsername=async ()=>{
    if(status===AsyncStatus.SUBMITTING) return;
    setStatus(AsyncStatus.SUBMITTING);

    if (!vUsername.onBlur()) {
      setStatus(AsyncStatus.IDLE);
      return;
    }

    try {
      await resetPassword(vUsername.value);
      setStatus(AsyncStatus.SUCCESS);
    } catch(err) {
      setStatus(AsyncStatus.FAIL);
    }
  }

  return (
    <div>
      <h1> </h1>
      <p>&#x2714;           .</p>
      <p>&#x2714;        .</p>
      {status===AsyncStatus.SUCCESS &&  <Alert variant='success'>   </Alert>}
      {status===AsyncStatus.FAIL && <Alert variant='danger'>   </Alert>}
      <TextField label='' name='username' {...vUsername} />
      <BlockButton label={status===AsyncStatus.SUBMITTING ? " ...":" "} onClick={handleResetUsername} styleName='danger' disabled={status===AsyncStatus.SUBMITTING} />
    </div>
  )
}

export default MemberResetPassword