import { useState } from 'react'
import { useNavigate } from 'react-router-dom';

import useUsername from '../../hooks/useUsername';
import usePassword from '../../hooks/usePassword';
import TextField from '../../components/common/TextField';
import BlockButton from '../../components/common/BlockButton';
import {login} from '../../utils/authApi'
import useAuthStore from '../../stores/useAuthStore';
import { AsyncStatus } from '../../utils/constants';
import { Alert } from 'react-bootstrap';

function MemberLogin() {
  // 1.   ( ,  ,     , )
  const [status, setStatus] = useState(AsyncStatus.IDLE);
  const [result, setResult] = useState(0);
  const vUsername = useUsername();
  const vPassword = usePassword();
  const navigate = useNavigate();

  // 2.    zustand store     setter
  const setLogin = useAuthStore(state=>state.setLogin);

  // 3.  
  const doLogin=async ()=>{
    if(status===AsyncStatus.SUBMITTING) return;
    setStatus(AsyncStatus.SUBMITTING);

    const r1 = await vUsername.onBlur();
    const r2 = vPassword.onBlur();
    if(!(r1 && r2)) {
      setStatus(AsyncStatus.IDLE);
      return;
    }

    const requestForm = {username:vUsername.value, password:vPassword.value};
    try {
      //   store    / 
      const response = await login(requestForm)
      console.log(response.data);
      setLogin(response.data);
      setStatus(AsyncStatus.IDLE);
      navigate("/");
      return;
    } catch(err) {
      setStatus(AsyncStatus.FAIL);
      //      : 401  , 403  
      setResult(err.status);
      console.log(err);
    } 
  };

  return (
    <div>
      <h1></h1>
      <div style={{height:300}}>
        <TextField label='' name="username" {...vUsername} />
        <TextField type='password' label='' name="password" {...vPassword}/>
        {(status===AsyncStatus.FAIL && result===401) && <Alert variant='danger'>  :     .</Alert>}
        {(status===AsyncStatus.FAIL && result===403) && <Alert variant='danger'>  :   .  .</Alert>}
      </div>
      <BlockButton label={status===AsyncStatus.SUBMITTING ? " ..." : ""} onClick={doLogin} styleName='primary' disabled={status===AsyncStatus.SUBMITTING} />
    </div>
  )
}

export default MemberLogin