import { useState } from "react";
import { Alert } from "react-bootstrap";

import useEmail from "../../hooks/useEmail";
import TextField from "../../components/common/TextField";
import BlockButton from "../../components/common/BlockButton";
import {findUsername} from '../../utils/memberApi';
import { AsyncStatus } from "../../utils/constants";

function MemberFindUsername() {
  // 1.   ( ,    ,    )
  const [status, setStatus] = useState(AsyncStatus.IDLE);
  const vEmail = useEmail();
  const [username, setUsername] = useState('');

  const init=()=>{
    //    
    setUsername("");
    setStatus(AsyncStatus.IDLE);
  }

  const handleFindUsername=async ()=>{ 
    if(status===AsyncStatus.SUBMITTING) return;
    setStatus(AsyncStatus.SUBMITTING);

    if (!vEmail.onBlur()) {
      setStatus(AsyncStatus.IDLE);
      return;
    }

    try {
      const response = await findUsername(vEmail.value);
      setUsername(response.data);
      setStatus(AsyncStatus.SUCCESS);
    } catch(err) {
      setStatus(AsyncStatus.FAIL);
      console.log(err);
    }
  }

  const onBlur = ()=>{
    init();
    vEmail.onBlur();
  }

  return (
    <div>
      <h1> </h1>
      <div style={{height:200}}>
        <TextField label='' name='email' value={vEmail.value} message={vEmail.message} onChange={vEmail.onChange} onBlur={onBlur} />
        {status===AsyncStatus.SUCCESS &&  <Alert variant='success'>  : {username}</Alert>}
        {status===AsyncStatus.FAIL && <Alert variant='danger'>  </Alert>}
      </div>
      <BlockButton label={status===AsyncStatus.SUBMITTING ? " ..." : " "} onClick={handleFindUsername} styleName='dark' disabled={status===AsyncStatus.SUBMITTING} />
    </div>
  )
}

export default MemberFindUsername