import { useNavigate } from 'react-router-dom'

function NotFound() {
  const navigate = useNavigate();
  return (
    <div>
      <h1>  ?</h1>
      <p>      .</p>
      <p>     </p>
      <button className='btn btn-dark' onClick={()=>navigate(-1)}> </button>
    </div>
  )
}

export default NotFound