import { useState } from "react";
import { Alert } from "react-bootstrap";

import BlockButton from "../../components/common/BlockButton";
import TextField from "../../components/common/TextField";
import ProfileField from "../../components/member/ProfileField";
import useConfirmPassword from "../../hooks/useConfirmPassword";
import useEmail from "../../hooks/useEmail";
import usePassword from "../../hooks/usePassword";
import usePhoto from "../../hooks/usePhoto";
import useUsername from "../../hooks/useUsername";
import {signup} from '../../utils/memberApi';
import { AsyncStatus } from "../../utils/constants";

function MemberSignup() {
  // 1.   ( , /// /  )
  const [status, setStatus] = useState(AsyncStatus.IDLE);
  const vProfile = usePhoto();
  const vUsername = useUsername(true);
  const vPassword = usePassword();
  const vConfirmPassword = useConfirmPassword(vPassword);
  const vEmail = useEmail();

  // 2.   :    ?
  const isSubmitting = status === AsyncStatus.SUBMITTING;

  // 3.  (   , , formData    )
  // - useCallback   
  //   useCallback  BlockButton     vUsername, vPassowrd     
  //   []  vUsername      
  //            handleMemberSingup   BlockButton   
  const handleMemberSignup=async()=>{
    if(isSubmitting) return;
    setStatus(AsyncStatus.SUBMITTING);

    const r1 = await vUsername.onBlur();
    const r2 = vPassword.onBlur();
    const r3 = vConfirmPassword.onBlur();
    const r4 = vEmail.onBlur();
    if (!(r1 && r2 && r3 && r4)) {
      setStatus(AsyncStatus.IDLE);
      return;
    }

    const formData = new FormData();
    formData.append('username', vUsername.value);
    formData.append('password', vPassword.value);
    formData.append('email', vEmail.value);
    formData.append('profile', vProfile.value);

    try {
      await signup(formData);
      setStatus(AsyncStatus.SUCCESS);
    } catch(err) {
      setStatus(AsyncStatus.FAIL);
      console.log(err);
    }
  };

  // 3.     or  
  if(status===AsyncStatus.SUCCESS) {
    return <Alert variant="success"> .  </Alert>
  }
  return (
    <>
      {status===AsyncStatus.FAIL &&  <Alert variant='danger'>  </Alert>}
      <ProfileField name='photo' label='' alt='' {...vProfile} />
      <TextField label='' name='username' {...vUsername} />
      <TextField label='' name='email' {...vEmail} />
      <TextField type='password' label='' name='password' {...vPassword} />
      <TextField type='password' label=' ' name='confirm-password' {...vConfirmPassword} />
      <BlockButton label={isSubmitting? "  ...":" "} onClick={handleMemberSignup} styleName='primary' disabled={isSubmitting}/>
    </>
  )
}

export default MemberSignup